import React, { useState, useEffect } from 'react';
import { 
  format, 
  parse, 
  set, 
  addMinutes,
  formatDistanceToNow 
} from 'date-fns'
import { 
  IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonGrid, IonRow, IonCol,
  IonCard, IonCardHeader, IonCardTitle, IonCardSubtitle, IonLabel, IonToggle,
  IonList, IonItem
} from '@ionic/react';
//import ExploreContainer from '../components/ExploreContainer';

if (!localStorage.getItem('clock_mode'))
    localStorage.setItem('clock_mode', 'AM/PM')

const CLOCK_MODE = localStorage.getItem('clock_mode')

const DATE_LONG = 'EEEE, MMMM d, yyyy'

const TIME = new Date().toLocaleString('en-US', {timeZone: 'America/Los_Angeles' })
const SERVER_TIME = parse(TIME, 'MM/dd/yyyy, h:mm:ss a', new Date())
const LOCAL_TIMEZONE = new Date().toString().split(' ')[5]
const GAME_TIMEZONE = 'GMT-0700'
const TIMEZONE_DIFF = 
  (parseInt(GAME_TIMEZONE.split('-')[1]) - parseInt(LOCAL_TIMEZONE.split('-')[1])) / 100
const dailyMissionsNextReset = {
  year: SERVER_TIME.getUTCFullYear(),
  month: SERVER_TIME.getUTCMonth(),
  date: SERVER_TIME.getDate() + 1,
  hours: 12 + TIMEZONE_DIFF,
  minutes: 59,
  seconds: 0
}

const DAILY = set(new Date(), dailyMissionsNextReset)
const SUPPLY_COUNTER = addMinutes(DAILY, 1)

const Clocks: React.FC = () => {

  const TIME_AP = 'h:mm:ss a'
  const TIME_24 = 'H:mm:ss'

  const [ time, setTime ] = useState<Date>(new Date())
  const [ daily ] = useState<Date>(DAILY)
  const [ supplyCounter ] = useState<Date>(SUPPLY_COUNTER)
  const [ timeFormat, setTimeFormat ] = useState<string>(CLOCK_MODE == 'AM/PM' ? TIME_AP : TIME_24)

  useEffect(() => {
    setTimeout(() => setTime(new Date()), 1000)
    localStorage.setItem('clock_mode', timeFormat == TIME_AP ? 'AM/PM' : '24 Hour')
  }, [ time, timeFormat ])

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Clocks</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonGrid className="ion-no-padding">
          <IonRow className="ion-justify-content-center">
            <IonCol size-md="6">
              <IonList>
                <IonItem>
                  <IonToggle
                    checked={ timeFormat == TIME_AP }
                    onIonChange={ e => setTimeFormat(e.detail.checked ? TIME_AP : TIME_24) } 
                  />
                  <IonLabel>{ timeFormat == TIME_AP ? 'AM/PM' : '24 Hour' }</IonLabel>
                </IonItem>
              </IonList>
              <IonCard>
                <IonCardHeader>
                  <IonCardSubtitle>Local Time</IonCardSubtitle>
                  <IonCardTitle>
                    { format(time, DATE_LONG) }
                  </IonCardTitle>
                  <IonCardTitle>{ format(time, timeFormat) }</IonCardTitle>
                </IonCardHeader>
              </IonCard>
              <IonCard>
                <IonCardHeader>
                  <IonCardSubtitle>Next Daily Mission Reset</IonCardSubtitle>
                  <IonCardTitle>
                    { format(daily, DATE_LONG) }
                  </IonCardTitle>
                  <IonCardTitle>
                    { format(daily, timeFormat) } 
                    ({ formatDistanceToNow(daily, { addSuffix: true }) })
                  </IonCardTitle>
                </IonCardHeader>
              </IonCard>
              <IonCard>
                <IonCardHeader>
                  <IonCardSubtitle>Next Daily Supply Counter Reset</IonCardSubtitle>
                  <IonCardTitle>
                    { format(supplyCounter, DATE_LONG) }
                    
                  </IonCardTitle>
                  <IonCardTitle>
                  { format(supplyCounter, timeFormat) }
                  ({ formatDistanceToNow(supplyCounter, { addSuffix: true }) })
                  </IonCardTitle>
                </IonCardHeader>
              </IonCard>
              <IonCard>
                <IonCardHeader>
                  <IonCardSubtitle>Next Daily Login Bonus Reset</IonCardSubtitle>
                  <IonCardTitle>
                    { format(daily, DATE_LONG) }
                  </IonCardTitle>
                  <IonCardTitle>
                    { format(daily, timeFormat) } 
                    ({ formatDistanceToNow(daily, { addSuffix: true }) })
                  </IonCardTitle>
                </IonCardHeader>
              </IonCard>
            </IonCol>
          </IonRow>
        </IonGrid>
      </IonContent>
    </IonPage>
  );
};

export default Clocks;
