import React, { useState, useEffect } from 'react';
import { 
  IonContent, 
  IonHeader, 
  IonPage, 
  IonTitle, 
  IonToolbar,
  IonIcon, IonLabel, IonButton,
  IonCard, IonCardHeader, IonCardSubtitle, IonCardTitle,
  IonGrid, IonRow, IonCol
} from '@ionic/react';

const Home: React.FC = () => {
  const [ gameNews, setGameNews ] = useState<[]>()

  const fetchGameNews = async() => {
    const response = await fetch('/database/news/game.json')
    const gameNews = await response.json()
    setGameNews(gameNews)
  }

  useEffect(() => {
    fetchGameNews()
  }, [])
  
  if (!gameNews) return (<div>Loading</div>)
  
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Home</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Home</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonGrid className="ion-no-padding">
          <IonRow className="ion-justify-content-center">
            <IonCol size-md="6">
            { gameNews.map((item: any) =>
              (
                <IonCard key={ item.id }>
                  <img src={item.cover} />
                  <IonCardHeader>
                    <IonCardTitle>{ item.title }</IonCardTitle>
                    <IonCardSubtitle>Game News</IonCardSubtitle>
                  </IonCardHeader>
                </IonCard>
              ))
            }
            </IonCol>
          </IonRow>
        </IonGrid>
      </IonContent>
    </IonPage>
  );
};

export default Home;
