import React, { createContext, useReducer, useEffect } from 'react'

const AppContext = createContext<any>({})

const initialState = {
  mobile_suits: [],
  loading: false,
}

const reducer = (state : any, action : any) => {
  switch (action.type) {
    case 'SET_MOBILE_SUITS':
      return { ...state, mobile_suits: action.payload }
    case 'SET_LOADING':
      return { ...state, loading: action.payload}
  }
  return state
}

const AppContextProvider = (props : any) => {
  const [ state, dispatch ] = useReducer(reducer, {...initialState})

  const fetchMobileSuits = async () => {
    const response = await fetch('/database/ms.json')
    const mobile_suits = await response.json()
    dispatch({type: 'SET_MOBILE_SUITS', payload: mobile_suits})
  }

  useEffect(() => {
    fetchMobileSuits()
  }, [])

  return (
    <AppContext.Provider value={{state, dispatch}}>
      {props.children}
    </AppContext.Provider>)
}

const AppContextConsumer = AppContext.Consumer

export {AppContext, AppContextProvider, AppContextConsumer }