import React, { useState, useEffect } from 'react';
import { 
  IonContent, 
  IonHeader, 
  IonPage, 
  IonTitle, 
  IonToolbar,
  IonGrid,
  IonRow,
  IonCol,
  IonList,
  IonItem,
  IonLabel,
  IonAvatar,
  IonChip,
  IonImg
} from '@ionic/react';
//import { AppContext } from '../store'

const Database: React.FC = () => {
  
  const [ mobile_suits, setMobileSuits ] = useState<[]>()

  const fetchMobileSuits = async () => { 
    const response = await fetch('/database/ms.json')
    const mobile_suits = await response.json()
    setMobileSuits(mobile_suits)
  }

  useEffect(() => { 
    fetchMobileSuits()
  }, [])

  if (!mobile_suits) return (<div>Loading</div>)

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Database</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Database</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonGrid className="ion-no-padding">
          <IonRow className="ion-justify-content-center">
            <IonCol size-md="6">
              <IonList>
                { mobile_suits.map((mobile_suit : any) => 
                  (
                  <IonItem key={mobile_suit.id} routerLink={`/ms/${mobile_suit.id}`}>
                    <IonAvatar slot="start">
                      <IonImg src={mobile_suit.avatar} />
                    </IonAvatar>
                    <IonLabel>
                      <h2>{mobile_suit.name}</h2>
                      <p>{mobile_suit.group} | {mobile_suit.cost}</p>
                    </IonLabel>
                    <IonChip outline color="dark" slot="end">
                      <IonLabel>LVL {mobile_suit.level}</IonLabel>
                    </IonChip>
                  </IonItem>
                  ))
                }
              </IonList>
            </IonCol>
          </IonRow>
        </IonGrid>
      </IonContent>
    </IonPage>
  );
};

export default Database;
