import React, { useState, useEffect } from 'react'
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonChip,
  IonLabel,
  IonCardHeader,
  IonCardSubtitle,
  IonButtons,
  IonBackButton
} from '@ionic/react'
//import { AppContext } from '../store'
import { RouteComponentProps } from 'react-router'

interface MSDetailPageProps extends RouteComponentProps<{
  id: string;
}>{}

const MS : React.FC<MSDetailPageProps> = ({ match }) => {

  const [ ms, setMs ] = useState<any>()
  const [ loading, setLoading ] = useState<boolean>(true)

  const fetchMobileSuits = async () => {
    
    const response = await fetch('/database/ms.json')
    const mobile_suits = await response.json()
    const mobile_suit = mobile_suits.find((suit : any) => suit.id == match.params.id)
    setMs(mobile_suit)
    setLoading(false)
    return () => setMs({})
  }

  useEffect(() => { 
    fetchMobileSuits()
  }) 

  if (loading) return (<div>Loading</div>)
  else
    return (
      <IonPage>
        <IonHeader>
          <IonToolbar>
            <IonButtons slot="start">
              <IonBackButton defaultHref="/database"></IonBackButton>
            </IonButtons>
            <IonTitle>{ms.name} LVL {ms.level}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonGrid className="ion-no-padding">
            <IonRow className="ion-justify-content-center">
              <IonCol size-md="6">
                <IonCardHeader>
                  <IonCardSubtitle>
                    <IonChip outline color="dark">
                      <IonLabel>{ms.group}</IonLabel>
                    </IonChip>
                    <IonChip outline color="dark">
                      <IonLabel>{ms.cost}</IonLabel>
                    </IonChip>
                    { ms.compatibility.map((tag: any, i: number) => (
                      <IonChip key={i} outline color="dark">
                        <IonLabel>{tag}</IonLabel>
                      </IonChip> 
                    ))}
                  </IonCardSubtitle>
                </IonCardHeader>
                <img src={ms.picture} alt={ms.name + ' ' + ms.level} />
              </IonCol>
            </IonRow>
          </IonGrid>
        </IonContent>
      </IonPage>
    )
}

export default MS